<td width="129" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">食尸者</span></p>
</td>
<td width="476" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">通过食尸，可以探索死者的记忆。</span></p>
</td>
</tr>
</table>
</div>
<p><span class="rvts43"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="106" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图</span><span class="rvts45">标</span></p>
</td>
<td width="226" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
