</td>
</tr>
<tr valign="top">
<td width="106" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber47.jpg"></p>
</td>
