</div>
<p class="rvps2"><span class="rvts43"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts21">精灵在初期具有额外的博学点数，这使得他们可以阅读和鉴定更高等级的物品。另外，精灵可以通过食尸，有机会获得额外的技能和任务。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



